import React, { useState } from 'react';
import './Form.css';

export default function Form() {

    const [contact, setContact] = useState('');
    const [vehicleType, setVehicleType] = useState([]);

    function handleContact(e) {
        e.preventDefault();
        setContact(e.target.value);
    }

    function handleVehicleType(e) {
        setVehicleType([...vehicleType, e.target.value]);
        console.log(vehicleType)
    }

    function handleSubmit(e) {
        e.preventDefault();
        // console.log(contact, vehicleType);
    }

    return (
        <div className='form'>

            <form onSubmit={handleSubmit}>
                <span id='head'>Book now</span>
                <input id='input' placeholder='Email or Mobile No' onChange={handleContact} value={contact}></input>
                <input onChange={handleVehicleType} id='select' value='Bike' type='radio' /><label id='label-radio'>Bike</label>
                <input onChange={handleVehicleType} id='select' value='Hatchback' type='radio' /><label id='label-radio'>Hatchback</label>
                <input onChange={handleVehicleType} id='select' value='Sedan' type='radio' /><label id='label-radio'>Sedan</label>
                <input onChange={handleVehicleType} id='select' value='SUV' type='radio' /><label id='label-radio'>SUV</label>
                {/* <select id='select' placeholder='Vehicle type' onChange={handleVehicleType} value={vehicleType}>
                    <option id='vehicleType'>Car</option>
                    <option id='vehicleType'>Bike</option>
                </select> */}
                <button id='submit'>Submit</button>
            </form>
        </div>
    )
}
